import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
    {
        path: "/",
        name: "Dashboard",
        component: () => import("../views/dashboard.vue").then(d => d.default)
    },
    {
        path: "/searchCards",
        name: "Search Cards",
        component: () => import("../views/cardSearch.vue").then(d => d.default)
    },
]

const router = new VueRouter({
    routes
})

export default router
